import { useState, useMemo } from 'react'
import './style.css'

const DemoWrapper = ({ title, demoMap }) => {
  const [current, setCurrent] = useState('')
  const Component = useMemo(() => demoMap[current], [current, demoMap])
  return (
    <div className="demo-wrapper">
      {!current && (
        <header>
          <h1>{title}</h1>
          <ul>
            {Object.keys(demoMap).map((demoName, index) => (
              <li key={index} onClick={() => setCurrent(demoName)}>
                {demoName}
              </li>
            ))}
          </ul>
        </header>
      )}
      {current && (
        <div>
          <div className="back" onClick={() => setCurrent('')}>
            back
          </div>
          <Component />
        </div>
      )}
    </div>
  )
}

export default DemoWrapper
